<template>
	<div style="height: 100%;">
		<div class="tab">
			<li :class="[tabIndex==0?'active':'']" @click="tab(0)">每日一练</li>
			<li :class="[tabIndex==1?'active':'']" @click="tab(1)">通关练习</li>
			<li :class="[tabIndex==2?'active':'']" @click="tab(2)">考试成绩</li>
			<div class="bottom" :style="bottomStyle"></div>
		</div>

		<div v-if="tabIndex==0">
			<div class="Rank_box">
				<div class="top">
					<div v-if="list.num==1" class="rank_icon first">{{list.num}}</div>
					<div v-if="list.num==2" class="rank_icon second">{{list.num}}</div>
					<div v-if="list.num==3" class="rank_icon third">{{list.num}}</div>

				<div v-if="list.num>3" class="rank_icon" style="color: #666666;">{{list.num}}</div>

					<div class="avatar">
						<img :src="list.image" />
					</div>
					<span style="margin-left: 0.2rem;">{{list.name}}</span>
					<span style="float: right;color: #666666;margin-right: 0.3rem;">{{list.result}}题</span>

				</div>
				<div class="refresh">
					<span>全班排名 ({{list.studentNum}}人)</span>
					<div class="re_btn">刷新</div>
				</div>
				<div v-for="item in list.ranking" class="top">

					<div v-if="item.num==1" class="rank_icon first">{{item.num}}</div>
					<div v-if="item.num==2" class="rank_icon second">{{item.num}}</div>
					<div v-if="item.num==3" class="rank_icon third">{{item.num}}</div>

					<div v-if="item.num>3" class="rank_icon" style="color: #666666;">{{item.num}}</div>

					<!--<span style="margin-left: 0.3rem;color: #666666;">100</span>-->
					<div class="avatar">
						<img :src="item.image" />

					</div>
					<span style="margin-left: 0.2rem;">{{item.name}}</span>
					<span style="float: right;color: #666666;margin-right: 0.3rem;">{{item.result}}题</span>

				</div>

			</div>

		</div>

		<div v-if="tabIndex==1">
			<div class="Rank_box">
				<div class="top">
					<div v-if="list.num==1" class="rank_icon first">{{list.num}}</div>
					<div v-if="list.num==2" class="rank_icon second">{{list.num}}</div>
					<div v-if="list.num==3" class="rank_icon third">{{list.num}}</div>

					<div v-if="list.num>3" class="rank_icon" style="color: #666666;">{{list.num}}</div>

					<div class="avatar">
						<img :src="list.image" />
					</div>
					<span style="margin-left: 0.2rem;">{{list.name}}</span>
					<div style="float: right;height: 100%;font-size: 0.24rem;color: #666666;line-height: 0.4rem;padding-right: 0.3rem;padding-top: 0.2rem;box-sizing: border-box;">
						<p style="width: 100%;">平均正确率{{list.result}}%</p>
						<p style="float: right;"><span style="font-size: 0.24rem;">进度:</span><span style="margin-left:0.1rem ;font-size: 0.24rem;">{{list.schedule}}</span></p>
					</div>

				</div>
				<div class="refresh">
					<span>全班排名 ({{list.studentNum}}人)</span>
					<div class="re_btn">刷新</div>
				</div>
				<div v-for="item in list.ranking" class="top">
					<div v-if="item.num==1" class="rank_icon first">{{item.num}}</div>
					<div v-if="item.num==2" class="rank_icon second">{{item.num}}</div>
					<div v-if="item.num==3" class="rank_icon third">{{item.num}}</div>

					<div v-if="item.num>3" class="rank_icon" style="color: #666666;">{{item.num}}</div>
					<!--<span style="margin-left: 0.3rem;color: #666666;">100</span>-->
					<div class="avatar">
						<img :src="item.image" />
					</div>
					<span style="margin-left: 0.2rem;">{{item.name}}</span>
					<div style="float: right;height: 100%;font-size: 0.24rem;color: #666666;line-height: 0.4rem;padding-right: 0.3rem;padding-top: 0.2rem;box-sizing: border-box;">
						<p style="width: 100%;">平均正确率{{item.result}}%</p>
						<p style="float: right;"><span style="font-size: 0.24rem;">进度:</span><span style="margin-left:0.1rem ;font-size: 0.24rem;">{{item.schedule}}</span></p>
					</div>

				</div>

			</div>

		</div>

		<div class="test_grade" v-if="tabIndex==2">
			<div v-if="!showRank">
				<div style="line-height: 1rem;background: #f8f8f8;padding:0 0.3rem;box-sizing: border-box;font-size: 0.3rem;">你参加过的考试</div>
				<div v-for="item in list" class="item" @click="showList(item.id)">
					<div class="left">
						<p style="font-size: 0.32rem;margin-top: 0.2rem;">{{item.title}}</p>
						<p v-if="item.type==1" style="font-size: 0.28rem;color: #666666;">类型:在线考试</p>
						<p v-if="item.type==2" style="font-size: 0.28rem;color: #666666;">类型:冲刺密卷</p>
					</div>
					<span style="">{{parseInt(item.score)}}分</span>
					<i></i>
				</div>

			</div>
			<div v-if="showRank">
				<div class="Rank_box">
					<div class="top">
					<div v-if="list.num==1" class="rank_icon first">{{list.num}}</div>
					<div v-if="list.num==2" class="rank_icon second">{{list.num}}</div>
					<div v-if="list.num==3" class="rank_icon third">{{list.num}}</div>

					<div v-if="list.num>3" class="rank_icon" style="color: #666666;">{{list.num}}</div>

					<div class="avatar">
						<img :src="list.image" />
					</div>
					<span style="margin-left: 0.2rem;">{{list.name}}</span>
					<span style="float: right;color: #666666;margin-right: 0.3rem;">{{list.result}}分</span>

				</div>
					<div class="refresh">
						<span>全班排名 ({{list.studentNum}}人)</span>
						<div class="re_btn">刷新</div>
					</div>
					<div v-for="item in list.ranking" class="top">

					<div v-if="item.num==1" class="rank_icon first">{{item.num}}</div>
					<div v-if="item.num==2" class="rank_icon second">{{item.num}}</div>
					<div v-if="item.num==3" class="rank_icon third">{{item.num}}</div>

					<div v-if="item.num>3" class="rank_icon" style="color: #666666;">{{item.num}}</div>

					<!--<span style="margin-left: 0.3rem;color: #666666;">100</span>-->
					<div class="avatar">
						<img :src="item.image" />

					</div>
					<span style="margin-left: 0.2rem;">{{item.name}}</span>
					<span style="float: right;color: #666666;margin-right: 0.3rem;">{{item.result}}分</span>

				</div>

				</div>
			</div>

		</div>

	</div>

</template>

<script>
	export default {

		data() {
			return {
				tabIndex: 0,
				showRank: false,
				list: {}
			}
		},
		computed: {
			bottomStyle() {
				if(this.tabIndex == 0) {
					return {
						"left": "12%"
					}
				} else if(this.tabIndex == 1) {
					return {
						"left": "45%"
					}
				} else if(this.tabIndex == 2) {
					return {
						"left": "78%"
					}
				}

			}
		},
		mounted() {
			this.getData(this.tabIndex + 1, 0)
		},
		methods: {
			tab(num) {
				this.tabIndex = num
				this.getData(num + 1, 0)
				this.showRank=false
			},
			showList(id){
				
				this.getData(this.tabIndex+1, id)
				this.showRank=true
			},
			getData(num, id) {
				this.list = {};
				if(id == 0) {
					var obj = {
						"s": this.$store.state.sessionId,
						"a": num
					}
				} else {
					var obj = {
						"s": this.$store.state.sessionId,
						"a": num,
						"id": id
					}

				}

				this.$Api.RankingList(obj).then((res) => {
					console.log(res)

					if(num < 3||id!=0) {
						this.list = res.q.rankings
						this.list.image = "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1522300298011&di=e4b387e3c04d878c58d6cb54385a73a6&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2014-09-23%2F000247589.jpg"
						
						for(var i = 0; i < this.list.ranking.length; i++) {

							this.list.ranking[i].image = "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1522300298011&di=e4b387e3c04d878c58d6cb54385a73a6&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2014-09-23%2F000247589.jpg"

						}
					} else {
						this.list = res.q.exampaper

					}

				})

			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	.tab {
		width: 100%;
		height: 0.88rem;
		list-style: none;
		position: relative;
		border-bottom: 0.01rem solid #CCCCCC;
		li {
			width: 33.33%;
			font-size: 0.3rem;
			text-align: center;
			line-height: 0.88rem;
			float: left;
		}
		.active {
			color: #fa8a12;
		}
		.bottom {
			width: 0.8rem;
			height: 0.04rem;
			background: #FA8A12;
			position: absolute;
			bottom: 0.02rem;
			left: 12%;
			transition: 0.5s;
		}
	}
	
	.item {
		width: 100%;
		height: 1.4rem;
		border-bottom: 0.02rem solid #CCCCCC;
		line-height: 1.4rem;
		position: relative;
		.left {
			float: left;
			padding-left: 0.3rem;
			box-sizing: border-box;
		}
		p {
			line-height: 0.5rem;
		}
		span {
			float: right;
			font-size: 0.3rem;
			color: #FA8A12;
			margin-right: 0.6rem;
		}
		i {
			width: 0.13rem;
			height: 0.23rem;
			background: url(../../assets/go.png) no-repeat;
			background-size: 100% 100%;
			position: absolute;
			right: 0.3rem;
			top: 43%;
		}
	}
	
	.Rank_box {
		width: 100%;
		.top {
			height: 1.2rem;
			line-height: 1.2rem;
			border-bottom: 0.02rem solid #CCCCCC;
			.rank_icon {
				margin-left: 0.3rem;
				float: left;
				width: 0.5rem;
				height: 0.64rem;
				font-size: 0.3rem;
				color: #FFFFFF;
				text-align: center;
				line-height: 0.8rem;
				margin-top: 0.2rem;
			}
			.first {
				background: url(../../assets/rank_1.png) no-repeat;
				background-size: 100% 100%;
			}
			.second {
				background: url(../../assets/rank_2.png) no-repeat;
				background-size: 100% 100%;
			}
			.third {
				background: url(../../assets/rank_3.png) no-repeat;
				background-size: 100% 100%;
			}
			span {
				float: left;
				font-size: 0.32rem;
			}
			.avatar {
				width: 0.7rem;
				height: 0.7rem;
				float: left;
				border-radius: 50%;
				margin-top: 0.2rem;
				margin-left: 0.2rem;
				img {
					width: 100%;
					height: 100%;
					float: left;
					border-radius: 50%;
				}
			}
		}
		.refresh {
			width: 100%;
			height: 1rem;
			line-height: 1rem;
			background: #f8f8f8;
			font-size: 0.3rem;
			padding-left: 0.3rem;
			box-sizing: border-box;
			.re_btn {
				width: 1.2rem;
				height: 0.55rem;
				line-height: 0.55rem;
				text-align: center;
				color: #FFFFFF;
				float: right;
				margin-right: 0.3rem;
				margin-top: 0.2rem;
				background: #fa8a12;
				border-radius: 0.55rem;
			}
		}
	}
</style>


